.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 90vh;
  justify-content: center;
  gap: 1rem;
}

.header {
  display: flex;
  gap: 0.5rem;
}

/* .header path {
  color: rgba(0, 255, 208, 0.837);
} */

.inputs {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.email,
.password {
  width: 20%;
  padding: 0.8rem;
  margin-bottom: 1rem;
  border: none;
  font-size: 1.3rem;
  border-radius: 0.5rem;
  background-color: rgb(0, 0, 0);
  box-shadow: 1px 1px 10px 1px rgb(0, 0, 0);
  color: white;
  &::placeholder {
    color: rgb(142, 142, 142);
  }
  &:focus {
    outline: none;
  }
}

.signup {
  background-color: green;
  box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.744);
  border: none;
  padding: 1rem;
  border-radius: 5rem;
  font-weight: bold;
  font-size: 1rem;
  color: rgb(255, 252, 252);

  &:hover {
    cursor: pointer;
    transform: scale(1.1);
    border-bottom-left-radius: 0;
    border-bottom-left-radius: 0;
    transition: 0.2s;
  }
  transition: ease-out 0.2s;
}
.errorMsg {
  background-color: rgba(255, 0, 0, 0.73);
  padding: 0.5rem;
  border-radius: 5rem;
  border-bottom-left-radius: 0rem;
  margin: 0;
}
@media screen and (max-width: 1200px) {
  .email,
  .password {
    width: 40%;
    padding: 0.6rem;
    font-size: 0.6rem;
  }
  .title {
    font-size: 1.4rem;
  }
  .signup {
    padding: 0.8rem;
    font-size: 0.6rem;
    width: 7rem;
  }
  .errorMsg {
    padding: 0.5rem;
  }
}
