.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 90vh;
  justify-content: center;
  gap: 2rem;
}

.header {
  display: flex;
  gap: 0.5rem;
}

/* .header path {
  color: rgba(0, 255, 208, 0.837);
} */

.email,
.password {
  width: 20%;
  padding: 1rem;
  margin-bottom: 2rem;
  border: none;
  font-size: 1.3rem;
  border-radius: 0.5rem;
  background-color: rgb(0, 0, 0);
  box-shadow: 1px 1px 10px 1px rgb(0, 0, 0);
  color: white;
  &::placeholder {
    color: rgb(142, 142, 142);
  }
  &:focus {
    outline: none;
  }
}

.inputs {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* justify-content: center; */
  align-items: center;
}

.Login {
  background-color: green;
  border: none;
  padding: 1rem;
  border-radius: 5rem;
  font-weight: bold;
  font-size: 1rem;
  color: rgb(255, 252, 252);
  /* color: transparent; */

  &:hover {
    cursor: pointer;
    transform: scale(1.1);
    border-bottom-left-radius: 0;
    transition: 0.2s;
  }
  transition: ease-out 0.2s;
}

.buttons {
  display: flex;
  gap: 1rem;
}

p {
  color: rgb(32, 29, 29);
}

.link {
  color: rgba(45, 163, 143, 0.837);
  &:hover {
    color: green;
  }
}

.errorMsg {
  background-color: rgba(255, 0, 0, 0.73);
  padding: 0.8rem;
  border-radius: 5rem;
  border-bottom-left-radius: 0rem;
}

.loading {
  width: 50%;
}

.loading span {
  color: rgba(93, 255, 228, 0.837);
}
@media screen and (max-width: 1200px) {
  .email,
  .password {
    width: 40%;
    padding: 0.7rem;
    font-size: 0.6rem;
  }
  .Welcometitle {
    font-size: 1rem;
  }
  .Login {
    padding: 0.7rem;
    font-size: 0.6rem;
    width: 7rem;
  }
  p {
    font-size: 1rem;
  }
  .errorMsg {
    padding: 0.6rem;
  }
}
