.container {
  display: flex;
  padding: 1rem;
  align-items: center;
}

.userDetailsOn,
.userDetailsOff {
  display: flex;
  gap: 1rem;
  width: 100%;
  padding: 0.4rem;

  background-color: rgba(250, 245, 245, 0.708);
  border-radius: 5rem;

  &:hover {
    background-color: rgba(0, 255, 208, 0.683);
    box-shadow: 1px 1px 10px 4px rgba(0, 0, 0, 0.489);

    transition: 0.2s;
    cursor: pointer;
    border-radius: 2rem;
    border-bottom-right-radius: 0rem;
    .userMessage,
    .userName {
      color: rgb(0, 0, 0);
    }
  }
  transition: ease-out 0.2s;
}

.userDetailsOn {
  box-shadow: 1px 1px 10px 0px rgba(0, 255, 208, 0.489);
}

.userDetailsOff {
  box-shadow: 1px 1px 10px 0px rgba(3, 174, 9, 0.489);
  &:hover {
    background-color: rgba(3, 174, 9, 0.489);
  }
}

.photo {
  background-color: rgb(69, 69, 69);
  padding: 2rem;
  border-radius: 5rem;
}

.details {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.userName {
  margin: 0;
  font-weight: bold;
  color: rgb(11, 10, 10);
}

.userMessage {
  margin: 10px 0 0 0;
  color: rgb(20, 35, 11);
}

.userStatus {
  display: flex;
  gap: 2rem;
}

@media screen and (max-width: 1200px) {
  .userDetailsOn,
  .userDetailsOff {
    gap: 0.4rem;
    padding: 0.4rem;
  }

  .photo {
    background-color: rgb(69, 69, 69);
    padding: 0.8rem;
    border-radius: 5rem;
  }

  .userName {
    font-size: 0.7rem;
    margin: 0;
    font-weight: bold;
    color: rgb(11, 10, 10);
  }
  .userMessage {
    font-size: 0.4rem;
    margin: 10px 0 0 0;
    color: rgb(20, 35, 11);
  }
}
